import * as React from "react";
import styled from "@emotion/styled";

const layoutWidth = 1280;
const layoutPadding = '1em';
const ContentBlock = styled.div`
    padding: 0 ${layoutPadding};
    max-width: ${layoutWidth}px;
    margin: 0 auto;
`;
const TextBlock = styled.div`
    /* text-align: justify; */

    &:after {
        content: ".";
        visibility: hidden;
        display: block;
        height: 0;
        clear: both;
    }

    > * + * {
        margin-top: 1em;
    }

    p {
        color: rgba(0, 0, 0, .5);
        font-size: .6em;
    }

    a {
        border-bottom: 1px solid;

        &:hover {
            color: #fff;
            background-color: rgba(0, 0, 0, .5);
        }
    }

    strong {
        font-weight: bold;
    }
`;
const YouTubeVideoBlock = styled.div`
    background-color: #eee;
    margin-top: 1em;
    line-height: 1;

    iframe {
        display: block;
        margin: 0 auto;
    }
`;
const ImagesGrid = styled.div`
    display: grid;
    /* grid-template-rows: repeat(8, 5vw); */
    /* grid-template-columns: repeat(4, 1fr); */
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 1em;

    img {
        display: block;
        width: 100%;
        height: 100%;
        /* width: 350px;
        height: 350px; */
        object-fit: cover;
    }
`;

const ImagesGroupsList = styled.ul`
    > * + * {
        margin-top: 1em;
    }
`;
const ImagesBlockWrapper = styled.div`
    > * + * {
        margin-top: 1em;
    }
`;
const VideoBlock = styled.div`
    background-color: #eee;

    video {
        max-width: 640px;
        margin: 0 auto;
        display: block;
    }
`;
const ImagesBlockHeader = styled.h3`
    color: rgba(0, 0, 0, .5);
    font-size: .75em;
    line-height: 1;
    padding-bottom: .25em;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
`;
const ContentWrapper = styled.div`
    color: rgba(0, 0, 0, .5);
    font-size: .6em;
    /* line-height: 1; */

    a {
        border-bottom: 1px solid;

        &:hover {
            color: #fff;
            background-color: rgba(0, 0, 0, .5);
        }
    }

    strong {
        font-weight: bold;
    }

    > * + * {
        margin-top: .5em;
    }
`;

function ImagesBlock(props: {imageGroup: ImageGroup}) {
    const {imageGroup} = props;

    return (
        <ImagesBlockWrapper>
            <ImagesBlockHeader>{imageGroup.title}</ImagesBlockHeader>

            {
                imageGroup.Content
                &&
                <ContentWrapper>
                    <imageGroup.Content />
                </ContentWrapper>
            }

            {
                imageGroup.gfycatId
                &&
                <VideoBlock>
                    <video
                        autoPlay={false}
                        playsInline={false}
                        preload="auto"
                        poster={`https://thumbs.gfycat.com/${imageGroup.gfycatId}-mobile.jpg`}
                        tabIndex={-1}
                        // width={1600}
                        // height={900}
                        controls={true}
                    >
                        <source src={`https://thumbs.gfycat.com/${imageGroup.gfycatId}-mobile.mp4`} type="video/mp4" />
                        <source src={`https://giant.gfycat.com/${imageGroup.gfycatId}.webm`} type="video/webm" />
                        <source src={`https://giant.gfycat.com/${imageGroup.gfycatId}.mp4`} type="video/mp4" />
                        <source src={`https://thumbs.gfycat.com/${imageGroup.gfycatId}-mobile.mp4`} type="video/mp4" />
                    </video>
                </VideoBlock>
            }

            <ImagesGrid>
                {
                    imageGroup.paths.map((imgPath, i) => {
                        return (
                            <div key={imgPath}>
                                <img
                                    loading="lazy"
                                    src={`${imageGroup.basePath}${imgPath}`}
                                    width={300}
                                    height={300}
                                />
                            </div>
                        );
                    })
                }
            </ImagesGrid>
        </ImagesBlockWrapper>
    );
}

