import * as React from "react";
import styled from "@emotion/styled";

import * as Icons from '../../icons';

const widthBreakpoint = '1280px';
const Wrapper = styled.div`
    display: flex;
    /* justify-content: center; */
    align-items: center;

    @media screen and (min-width: ${widthBreakpoint}) {
        > * + * {
            margin-left: 2em;
        }
    }

    img {
        border-radius: 100%;
        display: block;
        max-width: 200px;
        max-height: 200px;
    }
`;
const PhotoBlock = styled.div`
    display: none;

    @media screen and (min-width: ${widthBreakpoint}) {
        display: block;
    }
`;
const InfoBlock = styled.div`
    > * + * {
        margin-top: 1em;
    }
`;
const TopInfo = styled.div`
    color: #aaa;
    line-height: 1.5;
`;
const SocialIconsList = styled.ul`
    display: flex;
    line-height: 1;

    > * + * {
        /* margin-left: 1em; */
    }

    a {
        display: block;
        padding: .5em;
        transition: background-color .2s;

        svg {
            fill: rgba(0, 0, 0, .15);
            display: block;
            transition: fill .2s;
        }

        &:not(:hover) {
            background-color: #fff !important;
        }

        &:hover {
            svg {
                fill: #fff;
            }
        }
    }
`;

export default function HelloBlock() {
    const social = [
        {
            name: 'YouTube',
            Icon: Icons.YouTube,
            color: '#FF0000',
            url: 'https://www.youtube.com/channel/UCD4zBy-bxuTdt3UUMowjcaA',
        },
        {
            name: 'Twitter',
            Icon: Icons.Twitter,
            color: '#1DA1F2',
            url: 'https://twitter.com/LukGameDev',
        },
        {
            name: 'Discord',
            Icon: Icons.Discord,
            color: '#7289DA',
            url: 'https://discord.gg/s536z5Y',
        },
        {
            name: 'Instagram',
            Icon: Icons.Instagram,
            color: '#E4405F',
            url: 'https://www.instagram.com/_lukg_/',
        },
        // {
        //     name: 'Imgur',
        //     Icon: Icons.Imgur,
        //     color: '#1BB76E',
        //     url: 'https://imgur.com/user/lukg',
        // },
        // {
        //     name: 'ArtStation',
        //     Icon: Icons.ArtStation,
        //     color: '#13AFF0',
        //     url: 'https://www.artstation.com/lukaszgrolik',
        // },
        {
            name: 'GitHub',
            Icon: Icons.GitHub,
            color: '#181717',
            url: 'https://github.com/lukaszgrolik',
        },
    ];

    return (
        <Wrapper>
            <PhotoBlock>
                {/* <img src="https://source.unsplash.com/random/240x240" /> */}
                <img src="/images/photo_edit1_200x200.JPG" />
            </PhotoBlock>

            <InfoBlock>
                <TopInfo>
                    <p>Hi, I'm ≈Åukasz.</p>
                    <p>I live in Katowice, Poland.</p>
                    <p>I do programming, music and graphic art.</p>
                </TopInfo>

                <div>
                    <SocialIconsList>
                        {
                            social.map(icon => {
                                return (
                                    <li key={icon.name}>
                                        <a href={icon.url} title={icon.name} style={{backgroundColor: icon.color}}><icon.Icon /></a>
                                    </li>
                                )
                            })
                        }
                    </SocialIconsList>
                </div>
            </InfoBlock>
        </Wrapper>
    );
}
