import * as React from "react";
import styled from "@emotion/styled";

const layoutWidth = 1280;
const layoutPadding = '1em';
const ContentBlock = styled.div`
  padding: 0 ${layoutPadding};
  max-width: ${layoutWidth}px;
  margin: 0 auto;
`;
const TextBlock = styled.div`
  /* text-align: justify; */

  &:after {
    content: ".";
    visibility: hidden;
    display: block;
    height: 0;
    clear: both;
  }

  > * + * {
    margin-top: 1em;
  }

  a {
    border-bottom: 5px dotted;

    &:hover {
      border-bottom-style: solid;
    }
  }

  strong {
    font-weight: bold;
  }
`;
const imagesListSpacing = '.5em';
const imagesColumns = 4;
const ImagesList = styled.ul`
  line-height: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -${imagesListSpacing} -${imagesListSpacing} 0 0;

  > li {
    margin: ${imagesListSpacing} ${imagesListSpacing} 0 0;
    /* width: calc((${layoutWidth}px - 2 * ${layoutPadding} - ((${imagesColumns} - 1) * ${imagesListSpacing})) / ${imagesColumns}); */
  }

  img {
    display: block;
    width: 100%;
    max-height: 200px;
    transition: transform .2s;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, .25);

    &:hover {
      transform: scale(1.1);
    }
  }
`;
const ImagesList2 = styled.ul`
  line-height: 1;
  display: flex;
  flex-wrap: wrap;
  /* justify-content: center; */
  margin: -${imagesListSpacing} -${imagesListSpacing} 0 0;

  > li {
    margin: ${imagesListSpacing} ${imagesListSpacing} 0 0;
    /* width: calc((${layoutWidth}px - 2 * ${layoutPadding} - ((${imagesColumns} - 1) * ${imagesListSpacing})) / ${imagesColumns}); */

  }

  img {
    display: block;
    /* width: 100%; */
    /* height: 100%; */
    width: 200px;
    height: 200px;
    object-fit: cover;
    /* transition: transform .2s; */
    /* box-shadow: 3px 3px 10px rgba(0, 0, 0, .25); */

    &:hover {
      /* transform: scale(1.1); */
    }
  }
`;
const ImagesGrid = styled.div`
    display: grid;
    /* grid-template-rows: repeat(8, 5vw); */
    /* grid-template-columns: repeat(4, 1fr); */
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 1em;

    img {
        display: block;
        width: 100%;
        height: 100%;
        /* width: 350px;
        height: 350px; */
        object-fit: cover;
    }
`;

